'use client';

import type { MdxCodeExampleData } from '@mintlify/common';
import parse from 'html-react-parser';

import { CodeBlock } from '@/components/CodeBlock';
import { CodeGroup } from '@/components/CodeGroup';
import { StaticSpan } from '@/components/Static';

export const MdxCodeExample = ({ data }: { data: MdxCodeExampleData }) => {
  return (
    <CodeGroup isSmallText noMargins>
      {data.children?.map((child, i) => {
        return (
          <CodeBlock filename={child.filename} key={(child.filename ?? '') + i}>
            <StaticSpan>{parse(child.html ?? '')}</StaticSpan>
          </CodeBlock>
        );
      })}
    </CodeGroup>
  );
};
