'use client';

import parse from 'html-react-parser';
import Prism from 'prismjs';
import 'prismjs/components/prism-json';
import { ReactNode, useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { cn } from '@/utils/cn';
import { getCodeBlockScrollbarClassname } from '@/utils/getScrollbarClassname';

import { OtherResponseData } from '..';

type CodeResponseContentParams = {
  data: OtherResponseData;
};

export const jsonSyntaxHighlight = (json: object) => {
  return Prism.highlight(
    JSON.stringify(json, null, 2),
    Prism.languages.json as Prism.Grammar,
    'json'
  );
};

const MAX_BYTES = 90000; // 0.09 mb / 90 kb

export const CodeResponseContent = ({ data }: CodeResponseContentParams) => {
  const { docsConfig } = useContext(DocsConfigContext);
  let isJson = false;
  let tooBig = false;
  let content: ReactNode = data.content;
  try {
    const bytes = data.content.length;
    if (bytes < MAX_BYTES) {
      content = parse(jsonSyntaxHighlight(JSON.parse(data.content)));
    } else {
      tooBig = true;
      content = JSON.stringify(JSON.parse(data.content), null, 2);
    }
    isJson = true;
  } catch (e) {
    console.error(e);
  }

  const codeBlockMode = docsConfig?.styling?.codeblocks;

  return (
    <div className={cn('overflow-auto h-full', getCodeBlockScrollbarClassname(codeBlockMode))}>
      <div className="p-5 h-full whitespace-pre border-gray-200 dark:border-gray-700 dark:text-gray-300 font-mono text-xs leading-5">
        <span
          className={cn(
            codeBlockMode === 'system' ? 'language-json-auto' : 'language-json-dark',
            isJson ? '' : 'whitespace-normal',
            tooBig ? 'too-big' : ''
          )}
        >
          {content}
        </span>
      </div>
    </div>
  );
};
