export const getCodeBlockScrollbarClassname = (codeBlockTheme?: 'dark' | 'system') => {
  const classNames: string[] = ['scrollbar-thin scrollbar-thumb-rounded'];

  if (codeBlockTheme === 'system') {
    classNames.push(
      'scrollbar-track-gray-200 scrollbar-thumb-black/15 hover:scrollbar-thumb-black/20 active:scrollbar-thumb-black/20 dark:scrollbar-track-gray-800 dark:scrollbar-thumb-white/20 dark:hover:scrollbar-thumb-white/25 dark:active:scrollbar-thumb-white/25'
    );
  }

  if (codeBlockTheme === 'dark' || codeBlockTheme == undefined) {
    classNames.push(
      'scrollbar-thumb-white/20 dark:scrollbar-thumb-white/20 hover:scrollbar-thumb-white/25 dark:hover:scrollbar-thumb-white/25 active:scrollbar-thumb-white/25 dark:active:scrollbar-thumb-white/25'
    );
  }

  return classNames;
};

export const scrollbarClassname = [
  'scrollbar-thin scrollbar-thumb-rounded scrollbar-track-transparent',
  'scrollbar-thumb-black/20 dark:scrollbar-thumb-white/20',
  'hover:scrollbar-thumb-black/25 dark:hover:scrollbar-thumb-white/25',
  'active:scrollbar-thumb-black/25 dark:active:scrollbar-thumb-white/25',
];
