import { useState, useEffect } from 'react';

import { Ids } from '@/lib/local/identifiers';

export function useTitleLength(
  title: string,
  titleElementId: string = Ids.PageTitle,
  contextMenuButtonId: string = Ids.PageContextMenuButton
): boolean {
  const [isLongTitle, setIsLongTitle] = useState(false);

  useEffect(() => {
    let id: number | null;

    const checkTitleLength = () => {
      const titleElement = document.getElementById(titleElementId);
      if (titleElement) {
        const titleRect = titleElement.getBoundingClientRect();
        const parentRect = titleElement.parentElement?.getBoundingClientRect();
        const contextMenuButton = document.getElementById(contextMenuButtonId);
        const buttonRect = contextMenuButton?.getBoundingClientRect();
        const buttonWidth = buttonRect?.width || 0;

        if (parentRect && titleRect.width > parentRect.width - buttonWidth - 40) {
          setIsLongTitle(true);
        } else {
          setIsLongTitle(false);
        }
      }
    };

    const handleResize = () => {
      if (id) {
        cancelAnimationFrame(id);
      }
      id = requestAnimationFrame(checkTitleLength);
    };

    checkTitleLength();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      if (id) {
        cancelAnimationFrame(id);
      }
    };
  }, [title, titleElementId, contextMenuButtonId]);

  return isLongTitle;
}
