'use client';

import { potentiallyParseOpenApiString } from '@mintlify/common';
import { ChevronRightIcon } from 'lucide-react';

import { MethodPill } from '@/components/ApiExamples/MethodPill';
import { cn } from '@/utils/cn';

const headerClass = cn(
  'truncate',
  'text-base leading-[26px] text-gray-700 dark:text-gray-300',
  '[&_mark_b]:font-medium [&_mark_b]:text-md [&_mark]:bg-transparent [&_mark_b]:text-primary dark:[&_mark_b]:text-primary-light',
  '[&_span.font-medium]:text-primary dark:[&_span.font-medium]:text-primary-light'
);

export function SearchHit({
  isActive,
  header,
  description,
  metadata,
  duplicatedHeaders,
}: {
  isActive: boolean;
  header: string;
  description: string;
  metadata?: Record<string, unknown> | undefined;
  duplicatedHeaders?: Set<string>;
}) {
  const openapiString = metadata && 'openapi' in metadata ? String(metadata['openapi']) : undefined;
  const endpoint = !!openapiString ? potentiallyParseOpenApiString(openapiString) : undefined;

  const shouldUseExtendedBreadcrumbs = !!duplicatedHeaders?.has(header);
  const breadcrumbs = !!metadata
    ? generateBreadcrumbs(metadata, header, shouldUseExtendedBreadcrumbs)
    : [header];

  return (
    <div
      className={cn(
        'cursor-pointer relative rounded-xl flex gap-4 px-4 py-2 items-center',
        isActive && 'bg-gray-50 dark:bg-white/5'
      )}
    >
      <div className="flex flex-col w-[calc(100%-16px-20px)]">
        <div className="flex gap-1 items-center">
          {endpoint && <MethodPill method={endpoint.method} className="text-xs" />}
          <Breadcrumbs crumbs={breadcrumbs} />
        </div>
        {description && (
          <p
            className={cn(
              'text-sm truncate w-full text-gray-500 [&_mark]:text-gray-500 [&_mark]:bg-transparent',
              '[&_mark_b]:font-normal [&_mark_b]:text-primary dark:[&_mark_b]:text-primary-light',
              '[&_b_mark]:font-normal [&_b_mark]:text-primary dark:[&_b_mark]:text-primary-light',
              '[&_span.font-medium]:text-primary dark:[&_span.font-medium]:text-primary-light'
            )}
            dangerouslySetInnerHTML={{ __html: description }}
          ></p>
        )}
      </div>
      <div className="size-5">
        <ChevronRightIcon
          size={20}
          className={cn('text-transparent', isActive && 'text-primary dark:text-primary-light')}
        />
      </div>
    </div>
  );
}

function BreadcrumbIcon() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 24 24"
      width="16"
      height="16"
      fill="currentColor"
      className="mx-0.5 flex-shrink-0"
    >
      <path d="M13.1717 12.0007L8.22192 7.05093L9.63614 5.63672L16.0001 12.0007L9.63614 18.3646L8.22192 16.9504L13.1717 12.0007Z"></path>
    </svg>
  );
}

function Breadcrumbs({ crumbs }: { crumbs: string[] }) {
  return (
    <div className="flex items-center">
      {crumbs.map((crumb, index) => (
        <div key={index} className="flex items-center">
          {!!index && <BreadcrumbIcon />}
          <div className={headerClass} dangerouslySetInnerHTML={{ __html: crumb.trim() }}></div>
        </div>
      ))}
    </div>
  );
}

const generateBreadcrumbs = (
  metadata: Record<string, unknown>,
  hitHeader: string,
  generateFullBreadcrumbs: boolean
) => {
  const unmarkedTitle =
    'title' in metadata && typeof metadata.title === 'string' ? metadata.title : undefined;
  const breadcrumbs =
    'breadcrumbs' in metadata && Array.isArray(metadata.breadcrumbs)
      ? (metadata.breadcrumbs as string[])
      : undefined;
  if (!breadcrumbs) return [hitHeader];

  if (generateFullBreadcrumbs) {
    // Remove last crumb if the unmarked title of the chunk is the page title
    // We append the hit's header below and it doesn't need to be there twice.
    const generatedCrumbs =
      breadcrumbs.at(-1) === unmarkedTitle ? breadcrumbs.slice(0, -1) : breadcrumbs;

    // Some pages can be deeply nested so we only show up to 3 breadcrumbs.
    return generatedCrumbs.concat([hitHeader]).slice(-3);
  }

  // If the chunk represents a header within a page, generate breadcrumbs
  // that include the page title.
  if (breadcrumbs.at(-1) !== unmarkedTitle) {
    return breadcrumbs.concat([hitHeader]).slice(-2);
  } else {
    return [hitHeader];
  }
};
