'use client';

import parse from 'html-react-parser';
import Prism from 'prismjs';
import { ReactNode } from 'react';

export type CodeSnippetProps = {
  language?: string;
  /**
   * If `children` is a `string`, `Prism` will be emitted
   */
  children?: ReactNode;
};

export function CodeSnippet({ language, children }: CodeSnippetProps) {
  const grammar = language && Prism.languages[language];

  return (
    <pre>
      <code>
        {typeof children === 'string' && grammar
          ? parse(Prism.highlight(children, grammar, language))
          : children}
      </code>
    </pre>
  );
}
